@import url(https://rsms.me/inter/inter.css);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html {
  font-family: "Inter", sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: "Inter var", sans-serif;
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote {
  &:before,
  &:after {
    content: "";
    content: none;
  }
}

q {
  &:before,
  &:after {
    content: "";
    content: none;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

.container {
  width: 85%;
  max-width: 1290px;
  margin: 0 auto;
}

body {
  font-family: "Inter", "Fira Sans", sans-serif;
  font-size: 18px;
  line-height: 1.5em;
  color: #222;
}

html {
  border-top: 15px solid #222;
  border-bottom: 15px solid #222;
}

img {
  /*width: 100%;*/
  width: 100%;
  height: auto;
  border: 1px solid #d7d7d7;
  object-fit: cover;
}

h1,
h2 {
  font-size: 3em;
  font-weight: 700;
  line-height: 1.2;
  margin: 1em 0 0.66em 0;
  border-bottom: 5px solid #d7d7d7;
  padding-bottom: 28px;
}

.countries {
	display: grid;
	grid-template-columns: repeat(99, 1fr);
	margin: 1.6em 0 2.4em 0;
	color: #fff;
	text-align: right;
}

@media (max-width: 415px) {
  .countries {
    grid-template-columns: repeat(50, 1fr);
  }
}

.ships {
	display: grid;
	grid-template-columns: repeat(98, 1fr);
	margin: 1.6em 0 2.4em 0;
	color: #fff;
	text-align: right;
}

@media (max-width: 415px) {
  .ships {
		display: grid;
		grid-template-columns: repeat(50, 1fr);
		margin: 1.6em 0 2.4em 0;
		color: #fff;
		text-align: right;
	}
}
.title {
	font-size: .5em;
}

@media (max-width: 415px) {
	.container {
		overflow: hidden;
	}
	.zoom {
		position: relative;
		left: -30%;
		width: 175%;
    text-align: center;
    overflow: hidden;
	}
}

